const Calendario = () => {
    const [selectedHoliday, setSelectedHoliday] = useState(null);

    const handleDayPress = (day) => {
        const holiday = holidays[day.dateString];
        setSelectedHoliday(holiday);
    };

    const getMarkedDates = () => {
        const markedDates = {};
        Object.keys(holidays).forEach((date) => {
            markedDates[date] = {
                customStyles: {
                    container: { backgroundColor: 'green' },
                    text: { color: 'white' },
                },
            };
        });
        return markedDates;
    };

    const renderSelectedHoliday = () => {
        if (selectedHoliday) {
            return (
                <View style={styles.selectedHolidayContainer}>
                    <Text style={styles.selectedHolidayText}>
                        {selectedHoliday.phrase}
                    </Text>
                    <View style={styles.imageContainer}>
                        <Image
                            style={styles.image}
                            source={selectedHoliday.image}
                        />
                    </View>
                </View>
            );
        }
        return null;
    };

    return (

        <View style={styles.container}>
            <Calendar
                markedDates={getMarkedDates()}
                onDayPress={handleDayPress}
                markingType="custom"
                theme={{
                    calendarBackground: '#174b25',
                    textSectionTitleColor: 'white',
                    dayTextColor: 'white',
                    todayTextColor: 'white',
                    selectedDayTextColor: 'green',
                    monthTextColor: 'white',
                    arrowColor: 'white',
                }}
            />
            {renderSelectedHoliday()}
        </View>
    );
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        backgroundColor: '#174b25',
    },
    selectedHolidayContainer: {
        backgroundColor: '#174b25',
        flex: 1,
    },
    selectedHolidayText: {
        color: 'white',
        margin: 20,
    },
    imageContainer: {
        flex: 1,
        alignItems: 'center',
    },
    image: {
        width: 200,
        height: 300,
    },
});

export default Calendario;
