import React, { useState } from 'react';
import { View, Text, StyleSheet, Dimensions, PanResponder, TouchableOpacity } from 'react-native';

const SCREEN_WIDTH = Dimensions.get('window').width;

const Verdades = () => {
  const [currentScreen, setCurrentScreen] = useState(0);

  const panResponder = PanResponder.create({
    onStartShouldSetPanResponder: () => true,
    onPanResponderMove: (_, gestureState) => {
      const { dx } = gestureState;
      if (Math.abs(dx) > SCREEN_WIDTH / 3) {
        const newScreen = dx > 0 ? currentScreen - 1 : currentScreen + 1;
        if (newScreen >= 0 && newScreen < 20) {
          setCurrentScreen(newScreen);
        }
      }
    },
  });

  const sentences = [
    'Verdad 1\n \nLa verdadera democracia es aquella donde el gobierno hace lo que el pueblo quiere y defiende un solo interés: el del pueblo.',
    'Verdad 2\n \nEl Peronismo es esencialmente popular. Todo círculo político es antipopular, y por lo tanto, no es peronista.',
    'Verdad 3\n \nEl peronista trabaja para el Movimiento. El que en su nombre sirve a un círculo, o a un caudillo, lo es sólo de nombre.',
    'Verdad 4\n \nNo existe para el Peronismo más que una sola clase de hombres: los que trabajan.',
    'Verdad 5\n \nEn la Nueva Argentina el trabajo es un derecho que crea la dignidad del hombre y es un deber, porque es justo que cada uno produzca por lo menos lo que consume.',
    'Verdad 6\n \nPara un Peronista de bien, no puede haber nada mejor que otro Peronista.',
    'Verdad 7\n \nNingún Peronista debe sentirse más de lo que es, ni menos de lo que debe ser. Cuando un Peronista comienza a sentirse más de lo que es, empieza a convertirse en oligarca.',
    'Verdad 8\n \nEn la acción política la escala de valores de todo peronista es la siguiente: primero la Patria, después el Movimiento, y luego los Hombres.',
    'Verdad 9\n \nLa política no es para nosotros un fin, sino sólo el medio para el bien de la Patria, que es la felicidad de sus hijos y la grandeza nacional.',
    'Verdad 10\n \nLos dos brazos del Peronismo son la Justicia Social y la Ayuda Social. Con ellos damos al Pueblo un abrazo de justicia y de amor.',
    'Verdad 11\n \nEl Peronismo anhela la unidad nacional y no la lucha. Desea héroes pero no mártires.',
    'Verdad 12\n \nEn la Nueva Argentina los únicos privilegiados son los niños.',
    'Verdad 13\n \nUn gobierno sin doctrina es un cuerpo sin alma. Por eso el Peronismo tiene su propia doctrina política, económica y social: el Justicialismo.',
    'Verdad 14\n \nEl Justicialismo es una nueva filosofía de vida simple, práctica, popular, profundamente cristiana y profundamente humanista.',
    'Verdad 15\n \nComo doctrina política, el Justicialismo realiza el equilibrio del derecho del individuo con la comunidad.',
    'Verdad 16\n \nComo doctrina económica, el Justicialismo realiza la economía social, poniendo el capital al servicio de la economía y ésta al servicio del bienestar social.',
    'Verdad 17\n \nComo doctrina social, el Justicialismo realiza la Justicia Social, que da a cada persona su derecho en función social.',
    'Verdad 18\n \nQueremos una Argentina socialmente justa, económicamente libre, y políticamente soberana.',
    'Verdad 19\n \nConstituimos un gobierno centralizado, un Estado organizado y un pueblo libre.',
    'Verdad 20\n \nEn esta tierra lo mejor que tenemos es el Pueblo.',
    // ...agrega tus oraciones personalizadas aquí
  ];

  const screens = sentences.map((sentence, index) => (
    <View key={index} style={styles.screen}>
      <Text style={styles.screenText}>{sentence}</Text>
    </View>
  ));

  const goToPreviousScreen = () => {
    if (currentScreen > 0) {
      setCurrentScreen(currentScreen - 1);
    }
  };

  const goToNextScreen = () => {
    if (currentScreen < screens.length - 1) {
      setCurrentScreen(currentScreen + 1);
    }
  };



  return (
    <View style={styles.container} {...panResponder.panHandlers}>
      <View style={styles.screenContainer}>{screens[currentScreen]}</View>
      <View style={styles.buttonContainer}>
        <TouchableOpacity
          style={[styles.button, currentScreen === 0 && styles.disabledButton]}
          onPress={goToPreviousScreen}
          disabled={currentScreen === 0}
        >
          <Text style={styles.buttonText}>Anterior</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={[styles.button, currentScreen === screens.length - 1 && styles.disabledButton]}
          onPress={goToNextScreen}
          disabled={currentScreen === screens.length - 1}
        >
          <Text style={styles.buttonText}>Siguiente</Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#174b25',
  },
  screenContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  screenText: {
    fontSize: 24,
    fontWeight: 'bold',
    color: 'white',
    textAlign: 'center',
    margin: 20,
  },
  buttonContainer: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    paddingHorizontal: 20,
    paddingBottom: 20,
  },
  button: {
    backgroundColor: 'green',
    paddingVertical: 10,
    paddingHorizontal: 20,
    borderRadius: 8,
    margin: 25,
  },
  disabledButton: {
    opacity: 0.5,
  },
  buttonText: {
    fontSize: 18,
    fontWeight: 'bold',
    color: 'white',
  },
});

export default Verdades;
